module.exports = "<h1 id=contributing>Contributing</h1> <p>&#x1F44D;&#x1F389; First off, thanks for taking the time to contribute! &#x1F389;&#x1F44D;</p> <h2 id=playground-issue-reports>Playground &amp; Issue Reports</h2> <p>If you want to play with BootstrapVue components without any local setup just head to <a href=https://bootstrap-vue.js.org/play>OnlinePlayground</a> and you can interactively play and test components with a fresh Vue instance. If you want to keep your changes or make PRs reporting a component&apos;s misbehaviour you can save them in JSFiddle and provide that link in issues.</p> <h2 id=setup>Setup</h2> <ul> <li>Clone this repo (<code>git clone https://github.com/bootstrap-vue/bootstrap-vue --branch=dev</code>)</li> <li>Make sure you have node &amp; yarn installed locally.</li> <li><code>cd bootstrap-vue</code></li> <li>Run <code>yarn install</code> to get all dependencies installed.</li> </ul> <h2 id=work-on-components>Work on components</h2> <p>If you want to hack and improve components locally, you can follow these steps:</p> <ul> <li>Run <code>yarn docs-dev</code> to run a local development server.</li> <li>Head to <code>http://localhost:3000/play</code>.</li> <li>Now you can locally make changes to components (they are located in the <code>components</code> directory). Changes will be applied with webpack hot-reloading without needing to reload the page.</li> <li>Finally feel free to share your awesome hacks with others and opening a PR.</li> </ul> <h2 id=test-inside-your-project>Test inside your project</h2> <p>If you want to see your changes in your project instead of the playground:</p> <ul> <li>Execute <code>yarn link</code> inside <em>bootstrap-vue</em> directory.</li> <li>In your project run <code>yarn link bootstrap-vue</code></li> <li>Run <code>yarn watch</code> inside <em>bootstrap-vue</em></li> <li>Now every time you change a component, a new production version will be built and ready on your project. </li> </ul> <h2 id=pull-requests>Pull requests</h2> <p>Please ensure all pull requests related to <code>v1.x</code> are made aganst the <code>dev</code> branch on GitHub. See <a href=https://github.com/conventional-changelog/standard-version>standard-version</a> for commit guidelines and PR naming conventions.</p> ";

